import React, { Suspense, useState } from "react";
import {
  HashRouter as Router,
  Route,
  Switch,

} from "react-router-dom";

import Mainheader from "./components/mainheader";
import Home from "./home";
import Sidebar from "./components/sidebar";
import Appuserscreen from "./users/screen/Appuserscreen";
import { useSelector } from "react-redux";
const Le504 = React.lazy(() => import("./le504/home"));
const Le1100 = React.lazy(() => import("./le1100/home"));
const Le3500 = React.lazy(() => import("./le3500/home"));
const Le800 = React.lazy(() => import("./le800/home"));
const Collins = React.lazy(() => import("./lecollins/home"));
const Barrens = React.lazy(() => import("./lebarrens/home"));

function App() {
  const userlogin = useSelector((state) => state.userlogin);
  const [sidestate, sidegir] = useState(false);
  const [kolstate, kolgir] = useState(false);

  const sidebarclickhandler = () => {
    sidegir((p) => !p);
    setTimeout(() => {
      kolgir((p) => !p);
    }, 100);
  };
  const sidbaron = () => {
    setTimeout(() => {
      sidegir((p) => !p);
    }, 400);
    setTimeout(() => {
      kolgir((p) => !p);
    }, 300);
  };
 
  return (
    <Router>
      <Mainheader onClick={sidbaron} />
      {kolstate && (
        <Sidebar
          sidestate={sidestate}
          kolstate={kolstate}
          onClick={sidebarclickhandler}
        />
      )}
      <Suspense fallback={<div>Loading...</div>}>
        <Switch>
          <Route component={Home} exact path="/" />
          <Route path="/le504">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('le504') ? (<Le504 />) : (<div>لطفا طرح مورد نظر را خریداری نمایید</div>)}
          </Route>
          <Route path="/le1100">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('le1100') ? (<Le1100 />) : (<div>لطفا طرح مورد نظر را خریداری نمایید</div>)}
          </Route>
          <Route path="/le800">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('le800') ? (<Le800 />) : (<div>لطفا طرح مورد نظر را خریداری نمایید</div>)}
          </Route>
          <Route path="/le3500">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('le3500') ? (<Le3500 />) : (<div>لطفا طرح مورد نظر را خریداری نمایید</div>)}
          </Route>
          <Route path="/lecollins">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('lecollins') ? (<Collins />) : (<div>لطفا طرح مورد نظر را خریداری نمایید</div>)}
          </Route>
          <Route path="/lebarrens">
            {userlogin.userinfo &&userlogin.userinfo.userplan &&userlogin.userinfo.userplan.includes('lebarrens') ? (<Barrens />) : (<div>لطفا طرح مورد نظر را خریداری نمایید</div>)}
          </Route>
          <Route component={Appuserscreen} path="/users" />
        </Switch>
      </Suspense>
    </Router>
  );
}

export default App;
